// components/services/web-mobile/CaseStudies.tsx
"use client";

import Image from "next/image";
import { useState } from "react";

const caseStudies = [
  {
    id: 1,
    title: "eCommerce App for a Fashion Brand",
    description: "A complete Shopify-to-App transition with real-time inventory sync and custom push notifications.",
    image: "/images/case-studies/fashion-app.jpg",
    bgColor: "#0E0E0E",
    borderColor: "#232323",
    isSpecial: false
  },
  {
    id: 2,
    title: "Corporate Website for B2B SaaS",
    description: "Custom WordPress build with HubSpot CRM integration and blazing-fast performance scores.",
    image: "/images/case-studies/b2b-saas.jpg",
    bgColor: "#0E0E0E",
    borderColor: "#232323",
    isSpecial: true
  },
  {
    id: 3,
    title: "Healthcare Booking App",
    description: "HIPAA-compliant appointment booking app with doctor-patient video calls and in-app payments.",
    image: "/images/case-studies/healthcare-app.jpg",
    bgColor: "#0E0E0E",
    borderColor: "#232323",
    isSpecial: false
  },
  {
    id: 4,
    title: "Web Dashboard for AI Analytics",
    description: "React.js frontend + Node.js backend powering a real-time data dashboard for enterprise use.",
    image: "/images/case-studies/ai-dashboard.jpg",
    bgColor: "#0E0E0E",
    borderColor: "#232323",
    isSpecial: false
  }
];

export default function CaseStudies() {
  const [hoveredCard, setHoveredCard] = useState<number | null>(null);

  return (
    <section className="py-[80px] bg-white">
      <div className="max-w-[1440px] mx-auto px-[65px]">
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8">
          {caseStudies.map((study) => (
            <CaseStudyCard 
              key={study.id} 
              study={study}
              isHovered={hoveredCard === study.id}
              onHover={() => setHoveredCard(study.id)}
              onLeave={() => setHoveredCard(null)}
            />
          ))}
        </div>
      </div>
    </section>
  );
}

function CaseStudyCard({ 
  study, 
  isHovered,
  onHover,
  onLeave 
}: { 
  study: typeof caseStudies[0],
  isHovered: boolean,
  onHover: () => void,
  onLeave: () => void
}) {
  const isSpecial = study.isSpecial;
  
  return (
    <div 
      className="relative"
      style={{ 
        width: '327px',
        maxWidth: '100%',
        height: '378px',
        filter: 'drop-shadow(4px 4px 4px rgba(0, 0, 0, 0.25))'
      }}
      onMouseEnter={onHover}
      onMouseLeave={onLeave}
    >
      <div 
        className="relative w-full h-full text-white overflow-hidden transition-all duration-300"
        style={{ 
          background: isHovered ? '#608BF3' : study.bgColor,
          border: `3px solid ${isHovered ? '#608BF3' : study.borderColor}`,
          borderRadius: isSpecial ? '21px 21px 21px 163.5px' : '21px'
        }}
      >
        {/* Play button in top right */}
        <div className="absolute top-2 right-2 z-20">
          <div 
            className="rounded-full flex items-center justify-center cursor-pointer hover:scale-110 transition-transform"
            style={{
              width: '60.26px',
              height: '60.26px',
              background: '#0E0E0E',
              border: `1px solid ${study.borderColor || '#232323'}`,
              transform: 'rotate(-129.14deg)'
            }}
          >
            <span 
              className="text-white text-2xl"
              style={{ transform: 'rotate(129.14deg)' }}
            >
              ▶
            </span>
          </div>
        </div>
        
        {/* Content */}
        <div className="p-6 h-full flex flex-col">
          <h3 
            className="text-[20px] leading-[26px] mb-4"
            style={{ 
              fontFamily: 'Sequel Sans',
              fontWeight: 425
            }}
          >
            {study.title}
          </h3>
          
          {/* Divider */}
          <div 
            className="mb-4"
            style={{ 
              borderTop: '1.5px solid #FFFFFF',
              width: '278px'
            }}
          />
          
          <p 
            className="text-[12px] leading-[14px] mb-auto"
            style={{ 
              fontFamily: 'Sequel Sans',
              fontWeight: 405
            }}
          >
            {study.description}
          </p>
          
          {/* Image section at bottom */}
          <div 
            className="mt-4 -mx-6 -mb-6 overflow-hidden"
            style={{ 
              height: '182px',
              background: '#000000',
              borderRadius: isSpecial ? '0 0 21px 91px' : '0 0 21px 21px'
            }}
          >
            <Image
              src={study.image}
              alt={study.title}
              width={327}
              height={182}
              className="w-full h-full object-cover opacity-90"
            />
          </div>
        </div>
        
        {/* Large play button for special card */}
        {isSpecial && (
          <div 
            className="absolute flex items-center justify-center"
            style={{
              width: '101.72px',
              height: '101.72px',
              left: '-26px',
              bottom: '-26px',
              background: '#0E0E0E',
              border: '2px solid #FFFFFF',
              borderRadius: '50%',
              transform: 'rotate(-129.14deg)'
            }}
          >
            <span 
              className="text-white text-4xl"
              style={{ transform: 'rotate(129.14deg)' }}
            >
              ▶
            </span>
          </div>
        )}
      </div>
    </div>
  );
}