// components/services/web-mobile/FrequentlyAsked.tsx
"use client";

import { useState } from "react";

const faqs = [
  {
    id: 1,
    question: "How long does it take to build a website or app?",
    answer: "Basic websites: 1–2 weeks\nCustom platforms/apps: 4–12 weeks based on scope",
    isExpanded: true // First one expanded by default
  },
  {
    id: 2,
    question: "Will my site or app be mobile-friendly?",
    answer: "Absolutely. Every project is responsive across devices by default.",
    isExpanded: false
  },
  {
    id: 3,
    question: "Do you offer maintenance?",
    answer: "Yes — we provide optional monthly support, upgrades, and performance monitoring.",
    isExpanded: false
  },
  {
    id: 4,
    question: "Can I see progress during development?",
    answer: "Yes, we provide live staging links and weekly updates via Notion or Trello.",
    isExpanded: false
  }
];

export default function FrequentlyAsked() {
  const [openFaqs, setOpenFaqs] = useState<number[]>([1]); // First FAQ open by default

  const toggleFaq = (id: number) => {
    setOpenFaqs(prev => 
      prev.includes(id) 
        ? prev.filter(faqId => faqId !== id)
        : [...prev, id]
    );
  };

  return (
    <section className="py-16 md:py-20 bg-white">
      <div className="container mx-auto max-w-[1440px] px-4 sm:px-6">
        <h2 className="text-3xl md:text-4xl font-bold text-center mb-12 font-sequel" 
            style={{ fontSize: '40px', lineHeight: '129.61%' }}>
          Frequently Asked Questions
        </h2>
        
        <div className="space-y-6">
          {faqs.map((faq) => {
            const isOpen = openFaqs.includes(faq.id);
            const minHeight = isOpen && faq.id === 1 ? '143px' : '108px';
            
            return (
              <div key={faq.id} className="relative mx-auto" style={{ maxWidth: '1136px' }}>
                {/* Blue shadow card behind */}
                <div 
                  className="absolute rounded-2xl"
                  style={{ 
                    width: 'calc(100% - 7px)',
                    height: minHeight,
                    background: '#608BF3',
                    borderRadius: '17px',
                    left: '0px',
                    top: '0px'
                  }} 
                />
                
                {/* Main card */}
                <div 
                  className="relative rounded-2xl cursor-pointer transition-all duration-300"
                  style={{ 
                    backgroundColor: '#F5F6FA',
                    borderRadius: '17px',
                    minHeight: minHeight,
                    marginLeft: '6px'
                  }}
                  onClick={() => toggleFaq(faq.id)}
                >
                  <div className="p-6 pr-20">
                    <h3 className="text-xl font-semibold font-sequel pr-8" 
                        style={{ fontSize: '22px', lineHeight: '26px', fontWeight: 415 }}>
                      {faq.question}
                    </h3>
                    
                    {isOpen && (
                      <div className="mt-4">
                        {faq.answer.split('\n').map((line, index) => (
                          <p key={index} 
                             className="text-gray-700"
                             style={{ fontSize: '16px', lineHeight: '19px', fontWeight: 405 }}>
                            {line}
                          </p>
                        ))}
                      </div>
                    )}
                  </div>
                  
                  {/* Arrow button */}
                  <div 
                    className="absolute top-6 right-6 rounded-full flex items-center justify-center"
                    style={{ 
                      width: '42px',
                      height: '42px',
                      backgroundColor: '#608BF3'
                    }}
                  >
                    <svg 
                      width="24" 
                      height="24" 
                      viewBox="0 0 24 24" 
                      fill="none"
                      className={`transition-transform duration-300 ${isOpen ? '' : '-rotate-90'}`}
                    >
                      <path 
                        d="M7 10L12 15L17 10" 
                        stroke="white" 
                        strokeWidth="2" 
                        strokeLinecap="round" 
                        strokeLinejoin="round"
                      />
                    </svg>
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </section>
  );
}